import React, { useState, useEffect } from 'react';
import { Box, Typography, TextField, Button, Card, CardContent, CircularProgress, Checkbox, FormControlLabel, Link as MuiLink, Alert } from '@mui/material';
import { useNavigate, Link } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';

function Login() {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [rememberMe, setRememberMe] = useState(false);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const navigate = useNavigate();
  const { login, isAuthenticated, user } = useAuth();

  // 如果已经认证，直接跳转到首页
  useEffect(() => {
    console.log('Login组件 - isAuthenticated状态变化:', isAuthenticated);
    console.log('Login组件 - 当前用户:', user);
    if (isAuthenticated) {
      console.log('Login组件 - 准备跳转到首页');
      navigate('/home', { replace: true });
    }
  }, [isAuthenticated, navigate, user]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!username || !password) {
      setError('请输入用户名和密码');
      return;
    }

    setLoading(true);
    setError(''); // 清除组件内部的错误信息
    
    try {
      console.log('Login组件 - 开始调用 AuthContext login');
      await login({ username, password });
      console.log('Login组件 - AuthContext login 调用完成');
      // 页面跳转将在 useEffect 中根据 isAuthenticated 状态变化触发
      
    } catch (err) {
      console.error('Login组件 - 调用 AuthContext login 发生异常:', err);
    }
  };

  return (
    <Box sx={{ 
      display: 'flex', 
      justifyContent: 'center', 
      alignItems: 'center', 
      minHeight: '100vh',
      bgcolor: '#f5f5f5'
    }}>
      <Card sx={{ maxWidth: 400, width: '100%', mx: 2 }}>
        <CardContent sx={{ p: 4 }}>
          <Typography variant="h5" component="h1" align="center" gutterBottom>
            登录
          </Typography>
          
          {error && (
            <Alert 
              severity="error" 
              sx={{ 
                mb: 2,
                backgroundColor: '#ffebee',
                color: '#d32f2f',
                '& .MuiAlert-icon': {
                  color: '#d32f2f'
                },
                fontWeight: 'bold',
                fontSize: '1rem',
                py: 1.5,
                border: '1px solid #d32f2f',
                borderRadius: 1
              }}
            >
              {error}
            </Alert>
          )}
          
          <form onSubmit={handleSubmit}>
            <TextField
              margin="normal"
              required
              fullWidth
              id="username"
              label="用户名"
              name="username"
              autoComplete="username"
              autoFocus
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              error={!!error}
            />
            <TextField
              margin="normal"
              required
              fullWidth
              name="password"
              label="密码"
              type="password"
              id="password"
              autoComplete="current-password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              error={!!error}
            />
            <FormControlLabel
              control={
                <Checkbox
                  value="remember"
                  color="primary"
                  checked={rememberMe}
                  onChange={(e) => setRememberMe(e.target.checked)}
                />
              }
              label="记住我"
            />
            <Button
              type="submit"
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
              disabled={loading}
            >
              {loading ? <CircularProgress size={24} /> : '登录'}
            </Button>
            <Box sx={{ textAlign: 'center' }}>
              <Link to="/register" style={{ textDecoration: 'none' }}>
                <MuiLink component="span" variant="body2">
                  没有账号？立即注册
                </MuiLink>
              </Link>
            </Box>
          </form>
        </CardContent>
      </Card>
    </Box>
  );
}

export default Login; 