import React from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import { AuthProvider, useAuth } from './contexts/AuthContext';
import Layout from './components/Layout';
import Box from '@mui/material/Box';

// 页面组件
import Splash from './pages/Splash';
import Login from './pages/Login';
import Register from './pages/Register';
import Home from './pages/Home';
import Profile from './pages/Profile';
import ProfileEdit from './pages/ProfileEdit';
import MyPosts from './pages/MyPosts';
import Detail from './pages/Detail';
import RechargePaths from './pages/RechargePaths';
import Recharge from './pages/Recharge';
import Withdraw from './pages/Withdraw';
import Notification from './pages/Notification';
import TransactionNotification from './pages/TransactionNotification';
import Settings from './pages/Settings';
import SetPaymentPassword from './pages/SetPaymentPassword';
import CustomerServicePage from './pages/CustomerService';
import OperationResult from './pages/OperationResult';
import NotificationsPage from './pages/Notification';
import WalletPage from './pages/WalletPage';
import InvitedUsers from './pages/InvitedUsers';
import InvitedUserDetail from './pages/InvitedUserDetail';

// 受保护的路由组件
const ProtectedRoute = ({ children }) => {
  const { isAuthenticated, loading } = useAuth();
  
  if (loading) {
    return null; // 或者返回一个加载指示器
  }
  
  return isAuthenticated ? children : <Navigate to="/login" replace />;
};

// 路由配置数组
const routesConfig = [
  { path: '/home', element: <ProtectedRoute><Home /></ProtectedRoute>, title: '首页' },
  { path: '/profile', element: <ProtectedRoute><Profile /></ProtectedRoute>, title: '个人资料' },
  { path: '/profile/edit', element: <ProtectedRoute><ProfileEdit /></ProtectedRoute>, title: '编辑个人资料' },
  { path: '/my-posts', element: <ProtectedRoute><MyPosts /></ProtectedRoute>, title: '我的发布' },
  { path: '/detail/:id', element: <ProtectedRoute><Detail /></ProtectedRoute>, title: '信息详情' },
  { path: '/recharge-paths', element: <ProtectedRoute><RechargePaths /></ProtectedRoute>, title: '充值方式' },
  { path: '/recharge-paths/:pathId', element: <ProtectedRoute><Recharge /></ProtectedRoute>, title: '充值' },
  { path: '/withdraw', element: <ProtectedRoute><Withdraw /></ProtectedRoute>, title: '提现' },
  { path: '/notification', element: <ProtectedRoute><Notification /></ProtectedRoute>, title: '通知' },
  { path: '/transaction-notification', element: <ProtectedRoute><TransactionNotification /></ProtectedRoute>, title: '交易通知' },
  { path: '/settings', element: <ProtectedRoute><Settings /></ProtectedRoute>, title: '设置' },
  { path: '/set-payment-password', element: <ProtectedRoute><SetPaymentPassword /></ProtectedRoute>, title: '设置支付密码' },
  { path: '/customer-service', element: <ProtectedRoute><CustomerServicePage /></ProtectedRoute>, title: '在线客服' },
  { path: '/operation-result', element: <ProtectedRoute><OperationResult /></ProtectedRoute>, title: '操作结果' },
  { path: '/notifications', element: <NotificationsPage />, title: '通知中心' },
  { path: '/wallet', element: <ProtectedRoute><WalletPage /></ProtectedRoute>, title: '交易记录' },
  { path: '/invited-users', element: <ProtectedRoute><InvitedUsers /></ProtectedRoute>, title: '邀请用户' },
  { path: '/invited-users/:id', element: <ProtectedRoute><InvitedUserDetail /></ProtectedRoute>, title: '邀请用户详情' },
];

// 创建主题
const theme = createTheme({
  palette: {
    primary: {
      main: '#1976d2',
    },
    secondary: {
      main: '#dc004e',
    },
  },
  // 添加移动端适配
  components: {
    MuiButton: {
      styleOverrides: {
        root: {
          textTransform: 'none',
          minHeight: '36px', // 减小按钮最小高度
          padding: '6px 16px', // 减小按钮内边距
          fontSize: '0.875rem', // 减小字体大小
        },
      },
    },
    MuiTextField: {
      styleOverrides: {
        root: {
          '& .MuiInputBase-root': {
            minHeight: '36px', // 减小输入框最小高度
            fontSize: '0.875rem', // 减小字体大小
          },
        },
      },
    },
    MuiTypography: {
      styleOverrides: {
        h6: {
          fontSize: '1rem', // 减小标题字体大小
        },
        body1: {
          fontSize: '0.875rem', // 减小正文字体大小
        },
      },
    },
    MuiContainer: {
      styleOverrides: {
        root: {
          paddingLeft: '8px', // 减小容器左边距
          paddingRight: '8px', // 减小容器右边距
        },
      },
    },
    MuiAppBar: {
      styleOverrides: {
        root: {
          height: '48px', // 减小顶部导航栏高度
        },
      },
    },
    MuiToolbar: {
      styleOverrides: {
        root: {
          minHeight: '48px !important', // 强制设置工具栏最小高度
        },
      },
    },
  },
  breakpoints: {
    values: {
      xs: 0,
      sm: 360, // 降低小屏幕断点
      md: 768,
      lg: 1024,
      xl: 1280,
    },
  },
});

function App() {
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <AuthProvider>
        <Router basename={process.env.PUBLIC_URL}>
          <Box sx={{ display: 'flex', flexDirection: 'column', minHeight: '100vh' }}>
            <Box sx={{ flexGrow: 1, pb: 7 }}>
              <Routes>
                {/* 公共路由 */}
                <Route path="/" element={<Splash />} />
                <Route path="/login" element={<Login />} />
                <Route path="/register" element={<Register />} />
                
                {/* 受保护的路由 */}
                {routesConfig.map((route) => (
                  <Route 
                    key={route.path} 
                    path={route.path} 
                    element={
                      <ProtectedRoute>
                        <Layout>
                          {route.element}
                        </Layout>
                      </ProtectedRoute>
                    } 
                  />
                ))}

                {/* 处理所有其他路由，返回首页 */}
                <Route path="*" element={<Navigate to="/" replace />} />
              </Routes>
            </Box>
          </Box>
        </Router>
      </AuthProvider>
    </ThemeProvider>
  );
}

export default App;
export { routesConfig }; 