import React, { useState, useEffect } from 'react';
import { Box, Typography, TextField, Button, Container, CircularProgress, Card, CardMedia } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import apiService from '../services/api';

function Withdraw() {
  const navigate = useNavigate();
  const [amount, setAmount] = useState('');
  const [paymentPassword, setPaymentPassword] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [balance, setBalance] = useState(null);
  const [isPaymentPasswordSet, setIsPaymentPasswordSet] = useState(false);
  const [qrcodeFile, setQrcodeFile] = useState(null);
  const [qrcodePreview, setQrcodePreview] = useState(null);
  const [accountNumber, setAccountNumber] = useState('');
  const [accountName, setAccountName] = useState('');

  useEffect(() => {
    fetchBalance();
    checkPaymentPasswordStatus();
  }, []);

  const checkPaymentPasswordStatus = async () => {
    try {
      const response = await apiService.paymentPasswordService.getPaymentPasswordStatus();
      if (response.success) {
        setIsPaymentPasswordSet(response.data.hasPayPassword);
      }
    } catch (err) {
      console.error('获取支付密码状态失败:', err);
      setError(err.response?.data?.message || err.message || '获取支付密码状态失败');
    }
  };

  const fetchBalance = async () => {
    try {
      const response = await apiService.transactionService.getBalance();
      if (response.success) {
        setBalance(response.data.balance);
      } else {
        setError(response.message || '获取余额失败');
      }
    } catch (err) {
      setError(err.message || '获取余额失败');
      console.error('获取余额失败:', err);
    }
  };

  const handleQrcodeChange = (event) => {
    const file = event.target.files[0];
    if (file) {
      // 验证文件类型
      if (!file.type.startsWith('image/')) {
        setError('请上传图片文件');
        return;
      }
      // 验证文件大小（限制为5MB）
      if (file.size > 5 * 1024 * 1024) {
        setError('图片大小不能超过5MB');
        return;
      }
      setQrcodeFile(file);
      // 创建预览URL
      const previewUrl = URL.createObjectURL(file);
      setQrcodePreview(previewUrl);
      setError(''); // 清除之前的错误信息
    }
  };

  const handleWithdraw = async () => {
    if (!isPaymentPasswordSet) {
      navigate('/set-payment-password');
      return;
    }

    if (!qrcodeFile) {
      setError('请上传提现二维码');
      return;
    }

    if (!accountNumber || !accountName) {
      setError('请填写完整的收款信息');
      return;
    }

    setLoading(true);
    setError('');
    try {
      const formData = new FormData();
      formData.append('amount', parseFloat(amount));
      formData.append('paymentPassword', paymentPassword);
      formData.append('qrcode', qrcodeFile, qrcodeFile.name);
      formData.append('receiveAccount', accountNumber);
      formData.append('receiver', accountName);

      console.log('提现请求数据:', {
        amount: parseFloat(amount),
        receiveAccount: accountNumber,
        receiver: accountName,
        qrcodeFile: qrcodeFile.name
      });

      const response = await apiService.transactionService.createWithdraw(formData);
      if (response.success) {
        navigate('/operation-result', { state: { success: true, message: '提现申请已提交，请等待审核！' } });
      } else {
        setError(response.message || '提现失败');
      }
    } catch (err) {
      console.error('提现请求失败:', err);
      setError(err.response?.data?.message || err.message || '提现失败');
    } finally {
      setLoading(false);
    }
  };

  return (
    <Container component="main" maxWidth="sm" sx={{ mt: 4 }}>
      <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', p: 3, boxShadow: 3, borderRadius: 2 }}>
        <Typography variant="h5" component="h1" gutterBottom>
          提现
        </Typography>
        {balance !== null && (
          <Typography variant="subtitle1" color="text.secondary" sx={{ mb: 2 }}>
            当前余额: ¥{balance.toFixed(2)}
          </Typography>
        )}
        <TextField
          margin="normal"
          required
          fullWidth
          id="amount"
          label="提现金额"
          type="number"
          value={amount}
          onChange={(e) => setAmount(e.target.value)}
          sx={{ mb: 2 }}
        />
        <TextField
          margin="normal"
          required
          fullWidth
          id="accountNumber"
          label="收款账号"
          value={accountNumber}
          onChange={(e) => setAccountNumber(e.target.value)}
          sx={{ mb: 2 }}
        />
        <TextField
          margin="normal"
          required
          fullWidth
          id="accountName"
          label="收款人"
          value={accountName}
          onChange={(e) => setAccountName(e.target.value)}
          sx={{ mb: 2 }}
        />
        <TextField
          margin="normal"
          required
          fullWidth
          id="paymentPassword"
          label="支付密码"
          type="password"
          value={paymentPassword}
          onChange={(e) => setPaymentPassword(e.target.value)}
          sx={{ mb: 2 }}
        />
        
        {/* 二维码上传区域 */}
        <Box sx={{ width: '100%', mb: 2 }}>
          <Typography variant="subtitle1" gutterBottom>
            上传提现二维码
          </Typography>
          <input
            accept="image/*"
            style={{ display: 'none' }}
            id="qrcode-upload"
            type="file"
            onChange={handleQrcodeChange}
          />
          <label htmlFor="qrcode-upload">
            <Button
              variant="outlined"
              component="span"
              fullWidth
              sx={{ mb: 2 }}
            >
              选择二维码图片
            </Button>
          </label>
          {qrcodePreview && (
            <Card sx={{ mt: 2 }}>
              <CardMedia
                component="img"
                image={qrcodePreview}
                alt="提现二维码预览"
                sx={{ maxHeight: 200, objectFit: 'contain' }}
              />
            </Card>
          )}
        </Box>

        {error && (
          <Typography color="error" variant="body2" sx={{ mb: 2 }}>
            {error}
          </Typography>
        )}
        <Button
          type="button"
          fullWidth
          variant="contained"
          sx={{ mt: 2 }}
          onClick={handleWithdraw}
          disabled={loading || !amount || !paymentPassword || !qrcodeFile || !accountNumber || !accountName}
        >
          {loading ? <CircularProgress size={24} /> : '提交提现申请'}
        </Button>
      </Box>
    </Container>
  );
}

export default Withdraw; 