import React, { useState, useEffect, useCallback } from 'react';
import { 
  Box, 
  Typography, 
  CircularProgress, 
  List, 
  ListItem, 
  Container,
  Card,
  CardContent
} from '@mui/material';
import { useAuth } from '../contexts/AuthContext';
import apiService from '../services/api';

const ITEMS_PER_PAGE = 10;

function WalletPage() {
  const { user } = useAuth();
  const [transactions, setTransactions] = useState([]);
  const [loadingTransactions, setLoadingTransactions] = useState(true);
  const [transactionError, setTransactionError] = useState(null);
  const [currentPage, setCurrentPage] = useState(1);

  const fetchTransactions = useCallback(async (pageToFetch) => {
    if (!user) return;
    setLoadingTransactions(true);
    setTransactionError(null);
    try {
      const response = await apiService.userService.getMyTransactions({ page: pageToFetch, limit: ITEMS_PER_PAGE });
      if (response.success && Array.isArray(response.data)) {
        setTransactions(response.data);
        if (response.pagination) {
          setCurrentPage(response.pagination.currentPage);
        }
      } else {
        setTransactions([]);
        console.warn('No transactions found or data format incorrect.', response);
        setTransactionError(response.message || '交易记录加载失败或格式不正确');
      }
    } catch (err) {
      console.error('Failed to fetch transactions:', err);
      setTransactions([]);
      setTransactionError(err.message || '无法加载交易记录');
    } finally {
      setLoadingTransactions(false);
    }
  }, [user]);

  const getTransactionTypeDisplay = (type) => {
    const types = {
      'recharge': '充值',
      'withdraw': '提现',
      'purchase': '购买信息',
      'repay': '收益',
      'referral_commission': '推荐奖励',
      'REFERRAL_COMMISSION': '推荐奖励'
    };
    return types[String(type).toLowerCase()] || String(type).toUpperCase();
  };

  const isExpense = (type) => {
    const expenseTypes = ['withdraw', 'purchase'];
    return expenseTypes.includes(String(type).toLowerCase());
  };

  useEffect(() => {
    fetchTransactions(currentPage);
  }, [fetchTransactions, currentPage]);

  return (
    <Box sx={{ bgcolor: '#f7f8fa', minHeight: '100vh', pb: 4 }}>
      <Container maxWidth="md">
        <Card sx={{ mt: 3 }}>
          <CardContent>
            {transactionError && (
              <Typography color="error" sx={{ textAlign: 'center', mb: 2 }}>
                交易记录错误: {transactionError}
              </Typography>
            )}
            {loadingTransactions ? (
              <Box sx={{ display: 'flex', justifyContent: 'center', my: 3 }}>
                <CircularProgress />
              </Box>
            ) : transactions.length === 0 && !transactionError ? (
              <Typography sx={{ textAlign: 'center', color: 'text.secondary', py: 3 }}>
                暂无交易记录
              </Typography>
            ) : (
              <List disablePadding>
                {transactions.map((tx, index) => (
                  <React.Fragment key={tx._id || index}>
                    <ListItem 
                      sx={{ 
                        py: 2,
                        px: 0,
                        display: 'flex',
                        flexDirection: 'row',
                        alignItems: 'flex-start',
                        justifyContent: 'space-between',
                        borderBottom: index < transactions.length - 1 ? '1px solid #f0f0f0' : 'none'
                      }}
                    >
                      <Box sx={{ flex: 1 }}>
                        <Typography variant="subtitle1" sx={{ fontWeight: 500 }}>
                          {getTransactionTypeDisplay(tx.type)}
                        </Typography>
                        <Typography variant="caption" color="text.secondary" sx={{ display: 'block', mt: 0.5 }}>
                          {new Date(tx.createdAt).toLocaleString()}
                        </Typography>
                        {tx.remark && (
                          <Typography variant="caption" color="text.secondary" sx={{ display: 'block', mt: 0.5 }}>
                            备注: {tx.remark}
                          </Typography>
                        )}
                      </Box>
                      <Box sx={{ minWidth: 90, textAlign: 'right' }}>
                        <Typography
                          variant="h6"
                          sx={{
                            color: isExpense(tx.type) ? 'error.main' : 'success.main',
                            fontWeight: 600
                          }}
                        >
                          {isExpense(tx.type) ? '-' : '+'}￥{tx.amount ? tx.amount.toFixed(2) : '0.00'}
                        </Typography>
                        {typeof tx.balance === 'number' && (
                          <Typography variant="caption" color="text.secondary" sx={{ display: 'block', mt: 0.5 }}>
                            余额: {tx.balance.toFixed(2)}
                          </Typography>
                        )}
                      </Box>
                    </ListItem>
                  </React.Fragment>
                ))}
              </List>
            )}
          </CardContent>
        </Card>
      </Container>
    </Box>
  );
}

export default WalletPage; 