import React, { useState, useEffect, useCallback } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Box,
  Typography,
  CircularProgress,
  Paper,
  Card,
  CardContent,
  List,
  ListItem,
  ListItemText,
  ListItemIcon,
  Divider
} from '@mui/material';
import ChatIcon from '@mui/icons-material/Chat';
import { useAuth } from '../contexts/AuthContext';
import apiService from '../services/api';

function Notification() {
  const navigate = useNavigate();
  const { user, unreadCSCount } = useAuth();
  const [transactions, setTransactions] = useState([]);
  const [loadingTransactions, setLoadingTransactions] = useState(true);
  const [transactionError, setTransactionError] = useState(null);
  const [currentPage, setCurrentPage] = useState(1);
  const [showCustomerServiceNotificationCard, setShowCustomerServiceNotificationCard] = useState(false);

  useEffect(() => {
    if (unreadCSCount > 0) {
      setShowCustomerServiceNotificationCard(true);
    } else {
      setShowCustomerServiceNotificationCard(false);
    }
  }, [unreadCSCount]);

  const fetchTransactions = useCallback(async (pageToFetch) => {
    if (!user) return;
    setLoadingTransactions(true);
    setTransactionError(null);
    try {
      const response = await apiService.userService.getMyTransactions({ page: pageToFetch, limit: 10 });
      if (response.success && Array.isArray(response.data)) {
        setTransactions(response.data);
        if (response.pagination) {
          setCurrentPage(response.pagination.currentPage);
        }
      } else {
        setTransactions([]);
        console.warn('No transactions found or data format incorrect.', response);
        setTransactionError(response.message || '交易记录加载失败或格式不正确');
      }
    } catch (err) {
      console.error('Failed to fetch transactions:', err);
      setTransactions([]);
      setTransactionError(err.message || '无法加载交易记录');
    } finally {
      setLoadingTransactions(false);
    }
  }, [user]);

  const getTransactionTypeDisplay = (type) => {
    const types = {
      'recharge': '充值',
      'withdraw': '提现',
      'purchase': '购买信息',
      'repay': '收益'
    };
    return types[String(type).toLowerCase()] || String(type).toUpperCase();
  };
  
  const getStatusDisplay = (status) => {
    const statuses = {
      'pending': '处理中',
      'approved': '已通过',
      'rejected': '已拒绝',
      'completed': '已完成',
      'failed': '失败',
      'cancelled': '已取消'
    };
    return statuses[String(status).toLowerCase()] || String(status).toUpperCase();
  };

  useEffect(() => {
    fetchTransactions(currentPage);
  }, [fetchTransactions, currentPage]);

  const handleCustomerServiceCardClick = () => {
    setShowCustomerServiceNotificationCard(false);
    navigate('/customer-service');
  };

  if (loadingTransactions) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '50vh' }}>
        <CircularProgress />
      </Box>
    );
  }

  if (transactionError) {
    return (
      <Typography color="error" sx={{ textAlign: 'center', mt: 2 }}>
        错误: {transactionError}
      </Typography>
    );
  }

  return (
    <Box sx={{ maxWidth: 600, margin: 'auto', p: 2 }}>
      {showCustomerServiceNotificationCard && (
        <Card 
          sx={{ 
            mb: 2, 
            backgroundColor: 'primary.lighter',
            cursor: 'pointer',
            '&:hover': {
              boxShadow: 3,
            }
          }}
          onClick={handleCustomerServiceCardClick}
        >
          <CardContent sx={{ display: 'flex', alignItems: 'center', p: '12px !important' }}>
            <ListItemIcon sx={{ minWidth: 'auto', mr: 1.5, color: 'primary.main' }}>
              <ChatIcon />
            </ListItemIcon>
            <ListItemText 
              primary="您有未读客服消息"
              secondary="点击查看" 
              primaryTypographyProps={{ fontWeight: 'medium' }}
            />
          </CardContent>
        </Card>
      )}

      <Paper elevation={2}>
        {transactions.length === 0 ? (
          <Typography sx={{ textAlign: 'center', color: 'text.secondary', py: 3 }}>
            暂无交易记录
          </Typography>
        ) : (
          <List disablePadding>
            {transactions.map((tx, index) => (
              <React.Fragment key={tx._id || index}>
                <ListItem sx={{ py: 2 }}>
                  <ListItemText
                    primary={
                      <Typography variant="subtitle1">
                        {getTransactionTypeDisplay(tx.type)} - {getStatusDisplay(tx.status)}
                      </Typography>
                    }
                    secondary={
                      <Typography component="div">
                        <Box sx={{ mt: 1 }}>
                          <Typography
                            component="span"
                            variant="body2"
                            color={!['withdraw', 'purchase'].includes(String(tx.type).toLowerCase()) ? 'success.main' : 'error.main'}
                            sx={{ display: 'block', mb: 0.5 }}
                          >
                            金额: { !['withdraw', 'purchase'].includes(String(tx.type).toLowerCase()) ? '+' : '-'}￥{tx.amount ? Math.abs(tx.amount).toFixed(2) : '0.00'}
                          </Typography>
                          <Typography
                            component="span"
                            variant="caption"
                            color="text.secondary"
                            sx={{ display: 'block' }}
                          >
                            日期: {new Date(tx.createdAt).toLocaleString()}
                          </Typography>
                          {tx.remark && (
                            <Typography
                              component="span"
                              variant="caption"
                              color="text.secondary"
                              sx={{ display: 'block' }}
                            >
                              备注: {tx.remark}
                            </Typography>
                          )}
                          {tx.paymentMethod && (
                            <Typography
                              component="span"
                              variant="caption"
                              color="text.secondary"
                              sx={{ display: 'block' }}
                            >
                              方式: {tx.paymentMethod}
                            </Typography>
                          )}
                        </Box>
                      </Typography>
                    }
                  />
                </ListItem>
                {index < transactions.length - 1 && <Divider />}
              </React.Fragment>
            ))}
          </List>
        )}
      </Paper>
    </Box>
  );
}

export default Notification; 