import React, { useState, useEffect } from 'react';
import { useParams, useLocation, useNavigate } from 'react-router-dom';
import {
  Box,
  Card,
  CardContent,
  Typography,
  TextField,
  Button,
  CircularProgress,
  Divider,
} from '@mui/material';
import apiService, { baseStaticURL } from '../services/api';

function Recharge() {
  const { pathId } = useParams();
  const location = useLocation();
  const navigate = useNavigate();
  const [amount, setAmount] = useState('');
  const [rechargePath, setRechargePath] = useState(null);
  const [qrCodeImageUrl, setQrCodeImageUrl] = useState(null);
  const [screenshotFile, setScreenshotFile] = useState(null);
  const [loading, setLoading] = useState(true);
  const [submitting, setSubmitting] = useState(false);
  const [error, setError] = useState('');

  useEffect(() => {
    const selectedPath = location.state?.rechargePath;

    if (selectedPath && selectedPath._id === pathId) {
        setRechargePath(selectedPath);

        if (selectedPath.qrCodeUrl) {
            setQrCodeImageUrl(baseStaticURL + selectedPath.qrCodeUrl);
        } else {
             setQrCodeImageUrl(null);
        }

        setLoading(false);
    } else if (pathId) {
        setError('未能获取充值路径详情');
        setLoading(false);
    } else {
        setError('未指定充值路径');
        setLoading(false);
    }

  }, [pathId, location.state]);

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!amount || isNaN(amount) || Number(amount) <= 0) {
      setError('请输入有效的充值金额');
      return;
    }

    if (!rechargePath) {
      setError('充值方式信息缺失');
      return;
    }

    if (!screenshotFile) {
      setError('请上传交易截图');
      return;
    }

    setSubmitting(true);
    setError('');

    const formData = new FormData();
    formData.append('pathId', rechargePath._id);
    formData.append('amount', Number(amount));
    formData.append('proof', screenshotFile);

    try {
      const response = await apiService.rechargeService.submitRechargeOrder(formData);
      if (response.success) {
         setAmount('');
         setScreenshotFile(null);
         setError('');
         navigate('/operation-result', {
           state: {
             success: true,
             message: response.message || '充值订单已提交，请等待审核。',
             context: 'recharge_request',
             redirectTo: '/home'
           }
         });
      } else {
         setError(response.message || '提交充值订单失败');
      }
    } catch (err) {
      console.error('Error submitting recharge order:', err);
      setError(err.message || '提交充值订单失败');
    } finally {
      setSubmitting(false);
    }
  };

  const handleFileChange = (e) => {
    setScreenshotFile(e.target.files[0]);
  };

  if (loading) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100vh' }}>
        <CircularProgress />
      </Box>
    );
  }

  if (!rechargePath) {
       return (
         <Typography align="center" color="text.secondary" sx={{ p: 3 }}>
           未能加载充值方式详情。
         </Typography>
       );
  }

  return (
    <Box sx={{ bgcolor: '#f7f8fa', minHeight: '100vh' }}>
      <Box sx={{ maxWidth: 600, mx: 'auto', mt: 3, p: 2 }}>
        <Card>
          <CardContent>
            <Typography variant="h6" gutterBottom>
              充值方式: {rechargePath.name}
            </Typography>
            <Typography variant="body1" color="text.secondary" gutterBottom>
              {rechargePath.description}
            </Typography>

            <Divider sx={{ my: 2 }} />

            <Typography variant="h6" gutterBottom>收款信息</Typography>
            <Typography variant="body1">收款人: {rechargePath.account}</Typography>
            <Typography variant="body1">收款账号: {rechargePath.receiver}</Typography>

            {qrCodeImageUrl && (
                <Box sx={{ mt: 2, textAlign: 'center' }}>
                    <Typography variant="subtitle1">收款二维码:</Typography>
                    <img src={qrCodeImageUrl} alt="收款二维码" style={{ maxWidth: '200px', height: 'auto' }} />
                </Box>
            )}

            <Divider sx={{ my: 2 }} />

            <Typography variant="h6" gutterBottom>充值金额</Typography>
            <TextField
              fullWidth
              type="number"
              label="请输入充值金额"
              value={amount}
              onChange={(e) => setAmount(e.target.value)}
              InputProps={{
                startAdornment: <Typography sx={{ mr: 1 }}>¥</Typography>,
              }}
              sx={{ mb: 3 }}
            />

            <Typography variant="h6" gutterBottom>上传交易截图</Typography>
            <input
              type="file"
              accept="image/*"
              onChange={handleFileChange}
              style={{ display: 'block', marginBottom: '16px' }}
            />
            {screenshotFile && (
              <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
                已选择文件: {screenshotFile.name}
              </Typography>
            )}

            {error && !submitting && (
              <Typography color="error" sx={{ mb: 2 }}>
                {error}
              </Typography>
            )}

            <Button
              fullWidth
              variant="contained"
              onClick={handleSubmit}
              disabled={submitting || !rechargePath || !amount || !screenshotFile}
              sx={{ mt: 2 }}
            >
              {submitting ? <CircularProgress size={24} /> : '提交充值订单'}
            </Button>
          </CardContent>
        </Card>
      </Box>
    </Box>
  );
}

export default Recharge; 