import React, { useState, useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import {
  AppBar,
  Box,
  Toolbar,
  Typography,
  Container,
  IconButton,
  Menu,
  MenuItem,
  Badge,
  Divider,
  Paper,
  Link as MuiLink,
} from '@mui/material';
import {
  AccountCircle as AccountCircleIcon,
  Notifications as NotificationsIcon,
  Home as HomeIcon,
} from '@mui/icons-material';
import apiService from '../services/api';
import { useAuth } from '../contexts/AuthContext';
import { routesConfig } from '../App';

function Layout({ children }) {
  const navigate = useNavigate();
  const location = useLocation();
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [notificationCount, setNotificationCount] = React.useState(0);
  const [balance, setBalance] = useState(null);
  const [latestSystemNotification, setLatestSystemNotification] = useState(null);
  
  const { user, logout, unreadCSCount } = useAuth();

  useEffect(() => {
    const fetchBalance = async () => {
      try {
        const data = await apiService.transactionService.getBalance();
        setBalance(data.data.balance);
      } catch (error) {
        console.error('Failed to fetch balance:', error);
      }
    };

    if (anchorEl && user) {
      fetchBalance();
    } else if (!user) {
      setBalance(null);
    }
  }, [anchorEl, user]);

  // 获取未读通知数量
  useEffect(() => {
    const fetchUnreadCount = async () => {
      try {
        const res = await apiService.notificationService.getUnreadCount();
        if (res && typeof res.data === 'number') {
          setNotificationCount(res.data);
        } else if (res && typeof res.data?.count === 'number') {
          setNotificationCount(res.data.count);
        } else if (typeof res.count === 'number') {
          setNotificationCount(res.count);
        } else {
          setNotificationCount(0);
        }
      } catch (e) {
        setNotificationCount(0);
      }
    };
    fetchUnreadCount();
    const timer = setInterval(fetchUnreadCount, 60 * 1000);
    return () => clearInterval(timer);
  }, []);

  // 获取最新的系统通知 (仅在首页)
  useEffect(() => {
    const fetchLatestSystemNotification = async () => {
      if (location.pathname === '/home') {
        try {
          const response = await apiService.notificationService.getSystemNotifications({
            type: 'SYSTEM',
            status: 'ACTIVE',
            limit: 1,
            sortBy: 'createdAt',
            sortOrder: 'desc'
          });

          if (response && response.data && response.data.length > 0) {
            setLatestSystemNotification(response.data[0]);

            // 检查是否需要自动弹出 (仅在首页且未设置今日不再提示时)
            const isTodayNoShow = () => {
              const key = 'noShowAnnouncementDate';
              const today = new Date().toISOString().slice(0, 10);
              return localStorage.getItem(key) === today;
            };

            const shouldShowAutoPopup = !isTodayNoShow();

            if (shouldShowAutoPopup) {
              // 增加延迟时间，确保组件已经挂载完成
              setTimeout(() => {
                const event = new CustomEvent('showSystemNotificationDetail', { 
                  detail: response.data[0] 
                });
                window.dispatchEvent(event);
              }, 1000); // 增加到1秒
            }
          } else {
            setLatestSystemNotification(null);
          }
        } catch (error) {
          console.error('获取系统通知失败:', error);
          setLatestSystemNotification(null);
        }
      } else {
        setLatestSystemNotification(null);
      }
    };

    fetchLatestSystemNotification();
  }, [location.pathname]); // Dependency is location.pathname

  const handleSystemNotificationCardClick = () => {
    if (latestSystemNotification) {
      const isTodayNoShow = () => {
        const key = 'noShowAnnouncementDate';
        const today = new Date().toISOString().slice(0, 10);
        return localStorage.getItem(key) === today;
      };

      const event = new CustomEvent('showSystemNotificationDetail', { 
        detail: { 
          notification: latestSystemNotification, // 传递通知数据
          dontShowTodayStatus: isTodayNoShow() // 传递今日不再提示状态
        } 
      });
      window.dispatchEvent(event);
    } else {
      // Optionally, you could dispatch an event to Home.js to show a "No notification" message in its modal
      // const event = new CustomEvent('showSystemNotificationDetail', { detail: { title: "系统提示", content: "当前没有最新的系统公告。" } });
      // window.dispatchEvent(event);
    }
  };

  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleLogout = () => {
    handleClose();
    logout();
    navigate('/login');
  };

  const totalUnreadCount = notificationCount + unreadCSCount;

  // 根据当前路径查找对应的标题
  const currentRoute = routesConfig.find(route => {
    if (route.path.includes(':')) {
      const pathRegex = new RegExp(`^${route.path.replace(/\//g, '\\/').replace(/:\w+/g, '[^\\/]+')}$`);
      return pathRegex.test(location.pathname);
    }
    return route.path === location.pathname;
  });

  const pageTitle = currentRoute ? currentRoute.title : '加载中...';

  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', minHeight: '100vh' }}>
      <AppBar position="fixed">
        <Toolbar sx={{ minHeight: '48px !important' }}>
          <IconButton
            edge="start"
            color="inherit"
            aria-label="home"
            onClick={() => navigate('/home')}
            sx={{ mr: 1, p: 0.5 }}
          >
            <HomeIcon sx={{ fontSize: '1.25rem' }} />
          </IconButton>
          <Typography variant="h6" component="div" sx={{ flexGrow: 1, fontSize: '1rem' }}>
            {pageTitle}
          </Typography>
          <IconButton
            size="small"
            aria-label="show notifications"
            color="inherit"
            onClick={() => navigate('/notification')}
            sx={{ p: 0.5 }}
          >
            <Badge
              color="error"
              badgeContent={totalUnreadCount > 0 ? totalUnreadCount : null}
              max={99}
            >
              <NotificationsIcon sx={{ fontSize: '1.25rem' }} />
            </Badge>
          </IconButton>
          <IconButton
            size="small"
            aria-label="account of current user"
            aria-controls="menu-appbar"
            aria-haspopup="true"
            onClick={handleMenu}
            color="inherit"
            sx={{ p: 0.5 }}
          >
            <AccountCircleIcon sx={{ fontSize: '1.25rem' }} />
          </IconButton>
          <Menu
            id="menu-appbar"
            anchorEl={anchorEl}
            anchorOrigin={{
              vertical: 'bottom',
              horizontal: 'right',
            }}
            keepMounted
            transformOrigin={{
              vertical: 'top',
              horizontal: 'right',
            }}
            open={Boolean(anchorEl)}
            onClose={handleClose}
            PaperProps={{
              sx: {
                mt: 0.5,
                minWidth: '180px',
                '& .MuiMenuItem-root': {
                  fontSize: '0.875rem',
                  py: 1,
                },
              },
            }}
          >
            <MenuItem onClick={() => { handleClose(); navigate('/profile'); }}>
              <Typography variant="body2" sx={{ fontSize: '0.875rem' }}>
                {user ? (
                  <>
                    {user.role === 'admin' ? '管理员' : 
                     user.role === 'vip' ? 'VIP用户' : '普通用户'} - {user.username}
                  </>
                ) : '加载中...'}
              </Typography>
            </MenuItem>
            <MenuItem onClick={() => { handleClose(); navigate('/wallet'); }}>
              <Typography variant="body2" sx={{ fontSize: '0.875rem' }}>
                余额: {user ? (balance !== null ? `¥${balance.toFixed(2)}` : '加载中...') : '--'}
              </Typography>
            </MenuItem>
            <Divider />
            <MenuItem onClick={() => { handleClose(); navigate('/recharge-paths'); }}>充值</MenuItem>
            <MenuItem onClick={() => { handleClose(); navigate('/withdraw'); }}>提现</MenuItem>
            {user && user.role === 'vip' && (
              <MenuItem onClick={() => { handleClose(); navigate('/invited-users'); }}>
                我的邀请
              </MenuItem>
            )}
            <MenuItem onClick={() => { handleClose(); navigate('/customer-service'); }}>客服</MenuItem>
            <MenuItem onClick={handleLogout}>退出登录</MenuItem>
          </Menu>
        </Toolbar>
      </AppBar>

      <Box sx={{
        mt: '48px', // 对应AppBar高度
        flexGrow: 1,
        display: 'flex',
        flexDirection: 'column',
        px: { xs: 1, sm: 2 }, // 减小水平内边距
      }}>
        {location.pathname === '/home' && latestSystemNotification && (
          <Paper 
            elevation={3} 
            sx={{
              p: 1,
              mt: 0,
              mb: 1,
              mx: 0,
              backgroundColor: '#e3f2fd',
              cursor: 'pointer',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'space-between'
            }}
            onClick={handleSystemNotificationCardClick}
          >
            <Typography variant="subtitle2" sx={{ fontSize: '0.75rem', fontWeight: 'medium' }}>
              系统公告: {latestSystemNotification.title}
            </Typography>
            <MuiLink 
              component="button" 
              variant="body2" 
              sx={{ fontSize: '0.75rem' }}
              onClick={(e) => { 
                e.stopPropagation();
                handleSystemNotificationCardClick();
              }}
            >
              查看详情
            </MuiLink>
          </Paper>
        )}

        <Container 
          component="main" 
          sx={{ 
            flexGrow: 1, 
            py: 2, // 减小垂直内边距
            px: { xs: 1, sm: 2 }, // 减小水平内边距
            maxWidth: { xs: '100%', sm: '100%', md: '960px', lg: '1280px' } // 限制最大宽度
          }}
        >
          {children}
        </Container>
      </Box>
    </Box>
  );
}

export default Layout; 