import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Box,
  Typography,
  CircularProgress,
  Card,
  CardContent,
} from '@mui/material';
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';
import WalletIcon from '@mui/icons-material/AccountBalanceWallet';
import apiService, { baseStaticURL } from '../services/api';
import { useAuth } from '../contexts/AuthContext';

function RechargePaths() {
  const navigate = useNavigate();
  useAuth(); // 如果不需要使用 user 变量，直接调用 useAuth 即可
  const [rechargePaths, setRechargePaths] = useState([]);
  const [balance, setBalance] = useState(0); // 用户余额状态
  const [loadingPaths, setLoadingPaths] = useState(true);
  const [loadingBalance, setLoadingBalance] = useState(true);
  const [error, setError] = useState('');

  useEffect(() => {
    fetchRechargePaths();
    fetchBalance();
  }, []);

  const fetchRechargePaths = async () => {
    setLoadingPaths(true);
    setError('');
    try {
      // 调用获取充值路径的API
      const response = await apiService.rechargeService.getRechargePaths();
      console.log('[RechargePaths.js] API getRechargePaths response:', response); // LOG API RESPONSE
      // 假设后端返回的数据结构类似安卓：{ success: true, data: [...] }
      if (response.success && Array.isArray(response.data)) {
        setRechargePaths(response.data);
      } else {
        console.error('获取充值路径失败: 数据格式错误', response);
        setError('获取充值路径失败: 数据格式错误');
        setRechargePaths([]);
      }
    } catch (err) {
      console.error('获取充值路径错误:', err);
      setError(err.message || '获取充值路径失败');
      setRechargePaths([]);
    } finally {
      setLoadingPaths(false);
    }
  };

  const fetchBalance = async () => {
    setLoadingBalance(true);
    try {
      // 调用获取用户余额的API
      const response = await apiService.transactionService.getBalance();
      // 假设后端返回的数据结构：{ success: true, data: { balance: number } }
      if (response.success && typeof response.data?.balance === 'number') {
        setBalance(response.data.balance);
      } else {
        console.error('获取余额失败: 数据格式错误', response);
        setBalance(0); // 设置默认值
      }
    } catch (err) {
      console.error('获取余额错误:', err);
      setBalance(0); // 加载失败时设置默认值
    } finally {
      setLoadingBalance(false);
    }
  };

  const handlePathClick = (path) => {
    console.log('Navigating with path data:', path); // 添加日志输出
    // 跳转到充值详情页面，并将选中的路径信息传递过去
    navigate(`/recharge-paths/${path._id}`, { state: { rechargePath: path } });
  };

  const isLoading = loadingPaths || loadingBalance;

  return (
    <Box sx={{ bgcolor: '#f7f8fa', minHeight: '100vh', pb: 8 }}>
      {/* 余额显示卡片 */}
      <Card sx={{ mb: 2, mx: 2 }}>
        <CardContent>
          <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
             <WalletIcon sx={{ mr: 1, color: 'primary.main' }} />
             <Typography variant="h6">我的余额</Typography>
          </Box>
          <Typography variant="h4">
             ¥{loadingBalance ? '加载中...' : (typeof balance === 'number' ? balance.toFixed(2) : '0.00')}
          </Typography>
        </CardContent>
      </Card>

      {/* 充值路径列表 */}
      <Card sx={{ mx: 2 }}>
        <CardContent>
          <Typography variant="h6" gutterBottom>请选择充值方式</Typography>
          {isLoading ? (
            <Box sx={{ display: 'flex', justifyContent: 'center', p: 3 }}>
              <CircularProgress />
            </Box>
          ) : error ? (
            <Typography color="error" align="center">
              {error}
            </Typography>
          ) : rechargePaths.length === 0 ? (
            <Typography align="center" color="text.secondary">
              暂无可用充值方式
            </Typography>
          ) : (
            <Box>
              {rechargePaths.map((path) => {
                const iconPath = path.displayIcon || path.icon;
                const fullIconSrc = iconPath ? baseStaticURL + iconPath : null;
                console.log('[RechargePaths.js] Rendering path:', path.name, 'Icon field:', iconPath, 'Full src:', fullIconSrc, 'baseStaticURL:', baseStaticURL);
                return (
                  <Card key={path._id} sx={{ mb: 2, cursor: 'pointer' }} onClick={() => handlePathClick(path)}>
                    <CardContent>
                      <Box sx={{ display: 'flex', alignItems: 'center' }}>
                        {/* 图标 */}
                        <Box sx={{ mr: 2, display: 'flex', alignItems: 'center' }}>
                          {fullIconSrc ? (
                            <img src={fullIconSrc} alt={path.name} width={40} height={40} onError={(e) => { e.target.style.display = 'none'; console.error('Error loading image:', fullIconSrc); }} />
                          ) : (
                            // 没有图标时使用默认或不显示
                            <WalletIcon sx={{ width: 40, height: 40, color: 'text.secondary' }} />
                          )}
                        </Box>
                        {/* 名称和描述 */}
                        <Box sx={{ flexGrow: 1 }}>
                          <Typography variant="body1" fontWeight="bold">{path.name}</Typography>
                          <Typography variant="body2" color="text.secondary">{path.description}</Typography> {/* 假设有 description 字段 */}
                        </Box>
                        {/* 右侧箭头 */}
                        <ArrowForwardIosIcon sx={{ color: 'text.secondary' }} />
                      </Box>
                    </CardContent>
                  </Card>
                );
              })}
            </Box>
          )}
        </CardContent>
      </Card>
    </Box>
  );
}

export default RechargePaths; 