import React, { useState, useEffect, useCallback } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Box,
  Card,
  CardContent,
  Typography,
  CircularProgress,
  Grid,
  IconButton,
  Alert,
  Container,
  CardMedia
} from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';
import { useAuth } from '../contexts/AuthContext';
import apiService, { baseStaticURL } from '../services/api';
import Checkbox from '@mui/material/Checkbox';
import FormControlLabel from '@mui/material/FormControlLabel';

// Import Modal and Paper from Material UI
import Modal from '@mui/material/Modal';
import Paper from '@mui/material/Paper';
import CloseIcon from '@mui/icons-material/Close';

function Home() {
  const navigate = useNavigate();
  const { user } = useAuth();
  const [posts, setPosts] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [showAnnouncementModal, setShowAnnouncementModal] = useState(false);
  const [announcements, setAnnouncements] = useState([]);
  const [currentAnnouncementIndex, setCurrentAnnouncementIndex] = useState(0);
  const [dontShowToday, setDontShowToday] = useState(false);
  const [activeAds, setActiveAds] = useState([]);
  const [adPosition, setAdPosition] = useState([]);

  const loadPosts = useCallback(async () => {
    setLoading(true);
    setError('');

    try {
      const response = await apiService.postService.getPosts();

      // 检查响应是否成功并且包含嵌套的 data.list 字段
      if (response && response.success && response.data && Array.isArray(response.data.list)) {
        setPosts(response.data.list); // Access nested list
        setError(''); // 清除错误
      } else {
        // 处理响应格式不正确或 success 为 false 的情况
        console.error('Home组件 - API 响应数据格式错误或操作不成功:', response);
        setError(`加载帖子失败: 数据格式错误 或 ${response?.message || '未知错误'}`);
        setPosts([]); // 清空帖子列表
      }

    } catch (err) {
      console.error('Home组件 - 加载帖子异常:', err);
      // 使用从 api.js 传递过来的更详细的错误信息
      setError(`加载帖子失败: ${err.message || '未知错误'}`);
      setPosts([]); // 清空帖子列表
    } finally {
      setLoading(false);
    }
  }, []);

  // 新增：监听来自 Layout 的自定义事件以显示单条系统通知
  useEffect(() => {
    const handleShowSystemNotification = (event) => {
      const notificationData = event.detail;
      if (notificationData && notificationData.notification) {
        setAnnouncements([notificationData.notification]);
        setCurrentAnnouncementIndex(0);
        setShowAnnouncementModal(true);
        setDontShowToday(notificationData.dontShowTodayStatus || false);
      }
    };

    window.addEventListener('showSystemNotificationDetail', handleShowSystemNotification);

    return () => {
      window.removeEventListener('showSystemNotificationDetail', handleShowSystemNotification);
    };
  }, []);

  useEffect(() => {
    loadPosts();

    return () => {
      setAnnouncements([]);
      setShowAnnouncementModal(false);
      setCurrentAnnouncementIndex(0);
    };
  }, [loadPosts]);

  // Load ad settings
  useEffect(() => {
    const loadAd = async () => {
      try {
        const response = await apiService.systemService.getAds();
        if (response.success && response.data) {
          // 过滤出启用的广告
          const enabledAds = response.data.filter(ad => ad.status === 'active');
          if (enabledAds.length > 0) {
            // 处理所有广告的图片URL
            const processedAds = enabledAds.map(ad => ({
              ...ad,
              imageUrl: ad.imageUrl?.startsWith('http') ? ad.imageUrl : `${baseStaticURL}${ad.imageUrl}`
            }));
            
            setActiveAds(processedAds);
            
            // 如果有广告，随机设置广告位置
            if (posts.length > 0) {
              // 根据广告数量生成多个随机位置
              const positions = new Set();
              while (positions.size < Math.min(processedAds.length, Math.floor(posts.length / 2))) {
                positions.add(Math.floor(Math.random() * posts.length));
              }
              setAdPosition(Array.from(positions));
            }
          } else {
            setActiveAds([]);
            setAdPosition([]);
          }
        }
      } catch (err) {
        console.error('加载广告设置失败:', err);
        setActiveAds([]);
        setAdPosition([]);
      }
    };
    loadAd();
  }, [posts.length]);

  const handleDelete = async (id, event) => {
    event.stopPropagation(); // Prevent navigation when clicking delete button
    if (window.confirm('确定要删除这条信息吗？')) {
      try {
        await apiService.postService.deletePost(id);
        setPosts(posts.filter(post => post._id !== id));
      } catch (err) {
        setError(err.message || '删除失败');
      }
    }
  };

  // 关闭公告弹窗时，如果勾选了"今日不再提示"，写入 localStorage
  const handleCloseAnnouncementModal = () => {
    setShowAnnouncementModal(false);
    setCurrentAnnouncementIndex(0);
    setAnnouncements([]);
    if (dontShowToday) {
      const today = new Date().toISOString().slice(0, 10);
      localStorage.setItem('noShowAnnouncementDate', today);
    }
  };

  // 处理公告切换
  const handleAnnouncementChange = (index) => {
    setCurrentAnnouncementIndex(index);
  };

  // 处理上一页
  const handlePrevPage = () => {
    if (currentAnnouncementIndex > 0) {
      setCurrentAnnouncementIndex(currentAnnouncementIndex - 1);
    }
  };

  // 处理下一页
  const handleNextPage = () => {
    if (currentAnnouncementIndex < announcements.length - 1) {
      setCurrentAnnouncementIndex(currentAnnouncementIndex + 1);
    }
  };

  return (
    <Container maxWidth="lg" sx={{ py: 3 }}>
      <Box sx={{ mb: 3 }}>
        <Box sx={{ 
          display: 'flex', 
          flexDirection: { xs: 'column', sm: 'row' },
          justifyContent: 'space-between', 
          alignItems: { xs: 'stretch', sm: 'center' },
          gap: 2
        }}>
        </Box>

        {error && (
          <Alert severity="error" sx={{ mt: 2 }}>
            {error}
          </Alert>
        )}
      </Box>

      {loading && posts.length === 0 ? (
        <Box sx={{ display: 'flex', justifyContent: 'center', mt: 4 }}>
          <CircularProgress />
        </Box>
      ) : posts.length === 0 ? (
        <Typography align="center" sx={{ mt: 4 }}>
          没有找到相关信息。
        </Typography>
      ) : (
        <Grid container spacing={2}>
          {posts.map((post, index) => (
            <React.Fragment key={post._id}>
              {Array.isArray(adPosition) && adPosition.includes(index) && activeAds.length > 0 && (
                <Grid item xs={6} sm={6} md={4}>
                  <Card
                    sx={{ 
                      height: '100%', 
                      display: 'flex', 
                      flexDirection: 'column',
                      cursor: 'pointer',
                      position: 'relative',
                      '&:hover': {
                        boxShadow: 3,
                        transform: 'translateY(-2px)',
                        transition: 'all 0.3s ease'
                      }
                    }}
                    onClick={() => {
                      // 根据位置选择对应的广告
                      const adIndex = adPosition.indexOf(index);
                      if (adIndex !== -1 && activeAds[adIndex]) {
                        window.open(activeAds[adIndex].path, '_blank');
                      }
                    }}
                  >
                    <CardMedia
                      component="img"
                      height="200"
                      image={activeAds[adPosition.indexOf(index)]?.imageUrl}
                      alt="Advertisement"
                      sx={{
                        objectFit: 'cover',
                        width: '100%'
                      }}
                      onError={(e) => {
                        console.error('广告图片加载失败:', e);
                        console.log('尝试加载的图片URL:', activeAds[adPosition.indexOf(index)]?.imageUrl);
                      }}
                    />
                    <CardContent sx={{ flexGrow: 1 }}>
                      <Typography gutterBottom variant="h6" component="div" noWrap>
                        广告
                      </Typography>
                    </CardContent>
                  </Card>
                </Grid>
              )}
              <Grid item xs={6} sm={6} md={4}>
                <Card
                  sx={{ 
                    height: '100%', 
                    display: 'flex', 
                    flexDirection: 'column',
                    cursor: 'pointer',
                    position: 'relative',
                    '&:hover': {
                      boxShadow: 3
                    }
                  }}
                  onClick={() => navigate(`/detail/${post._id}`)}
                >
                  {user?.role === 'admin' && (
                    <IconButton
                      sx={{
                        position: 'absolute',
                        top: 8,
                        right: 8,
                        zIndex: 1,
                        backgroundColor: 'rgba(255, 255, 255, 0.8)',
                        '&:hover': {
                          backgroundColor: 'rgba(255, 255, 255, 0.9)',
                        },
                      }}
                      onClick={(e) => handleDelete(post._id, e)}
                      size="small"
                    >
                      <DeleteIcon fontSize="small" />
                    </IconButton>
                  )}
                  {post.imageUrls && post.imageUrls.length > 0 ? (
                    <Box
                      component="img"
                      src={`${baseStaticURL}${post.imageUrls[0]}`}
                      alt={post.title}
                      sx={{
                        width: '100%',
                        height: 200,
                        objectFit: 'cover',
                      }}
                    />
                  ) : (
                    <Box
                      component="img"
                      src="https://via.placeholder.com/150"
                      alt="Placeholder"
                      sx={{
                        width: '100%',
                        height: 200,
                        objectFit: 'cover',
                      }}
                    />
                  )}
                  <CardContent sx={{ flexGrow: 1 }}>
                    <Typography gutterBottom variant="h6" component="div" noWrap>
                      {post.title}
                    </Typography>
                    {post.loanAmount > 0 && (
                      <Typography variant="body2" color="text.secondary">
                        价格: ¥{post.loanAmount.toFixed(2)}
                      </Typography>
                    )}
                  </CardContent>
                </Card>
              </Grid>
            </React.Fragment>
          ))}
        </Grid>
      )}

      {/* Announcement Modal using Material UI Modal */}
      <Modal
        open={showAnnouncementModal}
        onClose={handleCloseAnnouncementModal}
        aria-labelledby="announcement-modal-title"
        aria-describedby="announcement-modal-description"
      >
        <Paper sx={{
          position: 'absolute',
          top: '50%',
          left: '50%',
          transform: 'translate(-50%, -50%)',
          width: { xs: '90%', sm: 400 },
          maxWidth: '100%',
          bgcolor: 'background.paper',
          boxShadow: 24,
          p: { xs: 2, sm: 4 },
          maxHeight: '80vh',
          overflowY: 'auto',
          borderRadius: 2,
        }}>
          <Box sx={{ 
            display: 'flex', 
            justifyContent: 'space-between', 
            alignItems: 'center', 
            mb: 2,
            borderBottom: '1px solid',
            borderColor: 'divider',
            pb: 1
          }}>
             <Typography id="announcement-modal-title" variant="h6" component="h2">
               最新公告 ({currentAnnouncementIndex + 1}/{announcements.length})
             </Typography>
             <IconButton onClick={handleCloseAnnouncementModal} size="small">
               <CloseIcon />
             </IconButton>
          </Box>

          <Box sx={{ position: 'relative', px: { xs: 1, sm: 3 } }}>
            {announcements.length > 1 && (
              <>
                <IconButton
                  onClick={handlePrevPage}
                  disabled={currentAnnouncementIndex === 0}
                  sx={{
                    position: 'absolute',
                    left: { xs: -8, sm: -12 },
                    top: '50%',
                    transform: 'translateY(-50%)',
                    color: 'text.secondary',
                    '&:hover': {
                      color: 'primary.main',
                      bgcolor: 'transparent',
                    },
                    '&.Mui-disabled': {
                      color: 'action.disabled',
                    },
                    zIndex: 1
                  }}
                >
                  <ChevronLeftIcon />
                </IconButton>
                <IconButton
                  onClick={handleNextPage}
                  disabled={currentAnnouncementIndex === announcements.length - 1}
                  sx={{
                    position: 'absolute',
                    right: { xs: -8, sm: -12 },
                    top: '50%',
                    transform: 'translateY(-50%)',
                    color: 'text.secondary',
                    '&:hover': {
                      color: 'primary.main',
                      bgcolor: 'transparent',
                    },
                    '&.Mui-disabled': {
                      color: 'action.disabled',
                    },
                    zIndex: 1
                  }}
                >
                  <ChevronRightIcon />
                </IconButton>
              </>
            )}

            <Box id="announcement-modal-description" sx={{ mt: 2, position: 'relative', minHeight: 150 }}>
              {announcements.length === 0 ? (
                <Typography variant="body2" color="text.secondary" align="center">
                  暂无公告
                </Typography>
              ) : (
                <Box 
                  sx={{ 
                    p: { xs: 1.5, sm: 2 },
                    bgcolor: 'background.default',
                    borderRadius: 1,
                    minHeight: 150,
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent: 'space-between'
                  }}
                >
                  <Box>
                    <Typography 
                      variant="subtitle1" 
                      component="h6"
                      sx={{ 
                        fontWeight: 'bold',
                        mb: 1.5,
                        color: 'primary.main',
                        fontSize: { xs: '1rem', sm: '1.1rem' }
                      }}
                    >
                      {announcements[currentAnnouncementIndex].title}
                    </Typography>
                    <Typography 
                      variant="body2" 
                      color="text.secondary" 
                      sx={{ 
                        whiteSpace: 'pre-wrap',
                        mb: 1,
                        lineHeight: 1.6,
                        fontSize: { xs: '0.875rem', sm: '0.9rem' }
                      }}
                    >
                      {announcements[currentAnnouncementIndex].content}
                    </Typography>
                  </Box>
                  {announcements[currentAnnouncementIndex].createdAt && (
                    <Typography 
                      variant="caption" 
                      color="text.secondary" 
                      display="block" 
                      sx={{ 
                        mt: 2,
                        textAlign: 'right',
                        fontSize: '0.75rem',
                        opacity: 0.8
                      }}
                    >
                      {new Date(announcements[currentAnnouncementIndex].createdAt).toLocaleString()}
                    </Typography>
                  )}
                </Box>
              )}
            </Box>
            {announcements.length > 1 && (
              <Box sx={{ 
                display: 'flex', 
                justifyContent: 'center', 
                gap: 1,
                mt: 2
              }}>
                {announcements.map((_, index) => (
                  <Box
                    key={index}
                    onClick={() => handleAnnouncementChange(index)}
                    sx={{
                      width: 6,
                      height: 6,
                      borderRadius: '50%',
                      bgcolor: currentAnnouncementIndex === index ? 'primary.main' : 'grey.300',
                      cursor: 'pointer',
                      transition: 'all 0.2s',
                      '&:hover': {
                        bgcolor: currentAnnouncementIndex === index ? 'primary.dark' : 'grey.400',
                      }
                    }}
                  />
                ))}
              </Box>
            )}
          </Box>
        </Paper>
      </Modal>
      {/* 复选框放在弹窗外部，居中，仅在弹窗显示时出现 */}
      {showAnnouncementModal && (
        <Box sx={{
          position: 'fixed',
          left: 0,
          right: 0,
          bottom: { xs: 24, sm: 40 },
          display: 'flex',
          justifyContent: 'center',
          zIndex: 1401 // 比modal略高
        }}>
          <FormControlLabel
            control={
              <Checkbox
                checked={dontShowToday}
                onChange={e => setDontShowToday(e.target.checked)}
                color="primary"
              />
            }
            label="今日不再提示"
            sx={{
              bgcolor: 'background.paper',
              px: 2,
              borderRadius: 2,
              boxShadow: 2,
              userSelect: 'none',
            }}
          />
        </Box>
      )}
    </Container>
  );
}

export default Home; 