import React, { useState } from 'react';
import {
  Box,
  Card,
  Typography,
  List,
  ListItem,
  ListItemText,
  ListItemSecondaryAction,
  Switch,
  Divider,
} from '@mui/material';
import { useNavigate } from 'react-router-dom';

function Settings() {
  const navigate = useNavigate();
  const [settings, setSettings] = useState({
    notifications: true,
    darkMode: false,
    autoPlay: true,
  });

  const handleSettingChange = (setting) => {
    setSettings(prev => ({
      ...prev,
      [setting]: !prev[setting],
    }));
  };

  return (
    <Box>
      <Typography variant="h6" gutterBottom>
        设置
      </Typography>

      <Card>
        <List>
          <ListItem>
            <ListItemText
              primary="消息通知"
              secondary="接收新消息和系统通知"
            />
            <ListItemSecondaryAction>
              <Switch
                edge="end"
                checked={settings.notifications}
                onChange={() => handleSettingChange('notifications')}
              />
            </ListItemSecondaryAction>
          </ListItem>

          <Divider />

          <ListItem>
            <ListItemText
              primary="深色模式"
              secondary="切换应用主题"
            />
            <ListItemSecondaryAction>
              <Switch
                edge="end"
                checked={settings.darkMode}
                onChange={() => handleSettingChange('darkMode')}
              />
            </ListItemSecondaryAction>
          </ListItem>

          <Divider />

          <ListItem>
            <ListItemText
              primary="自动播放"
              secondary="自动播放视频和音频"
            />
            <ListItemSecondaryAction>
              <Switch
                edge="end"
                checked={settings.autoPlay}
                onChange={() => handleSettingChange('autoPlay')}
              />
            </ListItemSecondaryAction>
          </ListItem>

          <Divider />

          <ListItem button onClick={() => navigate('/set-payment-password')}>
            <ListItemText
              primary="支付密码设置"
            />
            <ListItemSecondaryAction>
              {/* You can add an icon here if needed, e.g., <ArrowForwardIosIcon /> */}
            </ListItemSecondaryAction>
          </ListItem>
        </List>
      </Card>
    </Box>
  );
}

export default Settings; 